body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  background: #2d3748; /* 355C7D dark navy */
  min-height: 100vh;
}

input, textarea, button {
  font-family: inherit;
}

/* Card container for both main and view pages */
.card {
  max-width: 500px;
  margin: 40px auto;
  padding: 24px;
  border-radius: 18px;
  background: #35415a; /* 355C7D navy */
  box-shadow: 0 2px 16px #222a3a33;
  color: #fff;
  width: 90vw;
  box-sizing: border-box;
}

@media (max-width: 600px) {
  .card {
    margin: 16px auto;
    padding: 12px;
    border-radius: 12px;
    max-width: 98vw;
  }
  h2 {
    font-size: 1.3rem;
  }
  textarea {
    font-size: 1rem;
    padding: 8px;
  }
  button {
    font-size: 1rem;
    padding: 8px 16px;
  }
}

h2 {
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 20px;
  color: #FF7582; /* sunset pink */
}

textarea {
  width: 100%;
  margin-bottom: 12px;
  border-radius: 8px;
  border: 1.5px solid #725A7A;
  padding: 10px;
  font-size: 1.1rem;
  background: #2d3748;
  color: #fff;
  resize: vertical;
  box-sizing: border-box;
}

input[type="file"] {
  margin-bottom: 10px;
  color: #fff;
}

button {
  padding: 10px 28px;
  border-radius: 8px;
  background: #C56C86;
  color: #fff;
  font-weight: 700;
  border: none;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background 0.2s;
}
button:hover {
  background: #FF7582;
}

img {
  max-width: 100%;
  border-radius: 10px;
  box-shadow: 0 1px 8px #222a3a33;
  display: block;
  margin: 0 auto;
}

a {
  color: #FF7582;
  word-break: break-all;
}

strong {
  color: #C56C86;
}
